import { Inline, InlineProperties, Data } from '@ali/4ever-cangjie';

export interface TagData<T = Record<string, any>> extends Data {
  readonly tagType?: string;
  readonly height?: number;
  readonly width?: number;
  readonly metadata?: T;
}

export interface TagProperties {
  data?: TagData;
  nodes?: InlineProperties['nodes'];
}

export default class Tag extends Inline {
  static TYPE = 'tag';

  static create(properties: Tag | TagProperties | string) {
    if (typeof properties === 'string') {
      throw new Error('`Tag.create` does not accept string');
    }
    if (Tag.isTag(properties)) {
      return properties;
    }
    return super.create({
      ...properties,
      type: Tag.TYPE,
    }) as Tag;
  }

  static isTag(maybeTag: any): maybeTag is Tag {
    return Inline.isInline(maybeTag) && maybeTag.type === Tag.TYPE;
  }
}
